@font-face {
  font-family: 'Poppins';
  src: url('assets/fonts/Poppins/Poppins-Light.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
  font-family: 'Square_Peg';
  src: url('assets/fonts/Square_Peg/SquarePeg-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'Poppins', 'Square_Peg';
  --width: 295px;
    --height: 330px;
    --primary: #217e76;
    --text: #c8c7c7;
    --secondary: #f9f9f933;
    --radius: 20px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  width: 100%;
  height: 100vh;
  position: absolute;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
